var getRmb = require('../../common/requset.js');
export default {
    data() {
        return {
            type: '卖出',
            state: '',
            price: '',
            rmb: '',
            coffee: '',
            nuecv: '',
            italn: '',
            frte: true,
            checkall: '展示完整信息'
        };
    },
    onLoad(option) {
        console.log(option)
        var that = this;
        //获取上个页面携带的数据，并复制
        var italn = JSON.parse(option.cshug);
        that.italn = italn;
        var nuecv = that.italn[1];
        that.nuecv = that.italn[1];
        console.log(that.italn)
        that.state = italn[0][0].order_status;
        if (that.state == 104) {
            that.state = '已完成';
        }
        that.coffee = italn[0][0];
        console.log(that.coffee)
        that.price = italn[0][0].sale_money;
        that.rmb = getRmb.getrmb(that.price);
    },
    methods: {
        btn: function() {
            var that = this;
            that.frte = !that.frte;
            if (that.frte == false) {
                that.checkall = '收起';
            } else {
                that.checkall = '展示完整信息';
            }
        }
    }
};
